import { mapMutations } from 'vuex'
export default {
	data() {
		return {
			clientHeight: '',
			$src: this.$src
		};
	},
	onLoad() {
		// 储存第一次屏幕可视区高度
		uni.getSystemInfo({
			success: res => {
				this.clientHeight = res.windowHeight;
			}
		})
	},
	methods: {
		...mapMutations(["changePrice"]),
		goPay() {
			this.changePrice({price: 30});
			uni.navigateTo({
				url: '../components/wechatpay/wechatpay'
			})
		}
	}
}
